import React from 'react';
import Immutable from 'immutable';
import {storiesOf} from 'cs-web-components-storybook';
// DO NOT import components use relative path! Only use Registry or
// from component libs(defined externals)
import {Registry} from 'cs-web-components-base';
import * as constants from '../../../constants';

const ObjectCell = Registry.findComponent('cs-taskmanager-web-ObjectCell');

const MINIMAL_OBJECT = Immutable.Map({
    [constants.ICON_LINK]: '/resources/icons/byname/csweb_check',
    [constants.DESCRIPTION]: 'Example Object'
});
const FULL_OBJECT = Immutable.Map({
    [constants.ID]: 'object id link',
    [constants.ICON_LINK]: '/resources/icons/byname/csweb_check',
    [constants.UI_LINK]: '#',
    [constants.DESCRIPTION]: 'Example Object'
});

storiesOf('ObjectCell', {})
    .add('Minimal Object', () => (
        <ObjectCell value={MINIMAL_OBJECT} />
    ))
    .add('Full Object', () => (
        <ObjectCell value={FULL_OBJECT} />
    ))
    .add('None', () => (
        <ObjectCell />
    ))
;



