import React from 'react';
import {storiesOf} from 'cs-web-components-storybook';
// DO NOT import components use relative path! Only use Registry or
// from component libs(defined externals)
import {Registry} from 'cs-web-components-base';

const OverdueCell = Registry.findComponent('cs-taskmanager-web-OverdueCell');

storiesOf('OverdueCell', {})
    .add('Overdue', () => (
        <OverdueCell value />
    ))
    .add('Not Overdue', () => (
        <OverdueCell />
    ))
    .add('Overdue, isGroup', () => (
        <OverdueCell value isGroup />
    ))
    .add('Not Overdue, isGroup', () => (
        <OverdueCell isGroup />
    ))
;



